import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: "HomeView",
        component: HomeView,
        children: [{
                path: "/",
                name: "BookCity",
                component: () =>
                    import ("../views/BookCity.vue")
            }, {
                path: "/BookCase",
                name: "BookCase",
                // 异步懒加载
                component: () =>
                    import ("../views/BookCase.vue")
            }, {
                path: "/BookClassify",
                name: "BookClassify",
                component: () =>
                    import ("../views/BookClassify.vue")
            },
            {
                path: "/MyView",
                name: "MyView",
                component: () =>
                    import ("../views/MyView.vue")
            },
        ],
    },
    {
        path: "/SearchView",
        name: "SearchView",
        // 异步懒加载
        component: () =>
            import ("../views/SearchView.vue")
    },
    {
        path: "/ReadContent/:id/:sid/",
        name: "ReadContent",
        // 异步懒加载
        component: () =>
            import ("../views/ReadContent.vue")
    },
    {
        path: "/BookDetails/:id/:title/",
        name: "BookDetails",
        // 异步懒加载
        component: () =>
            import ("../views/BookDetails.vue")
    },
    {
        path: '/BookCatalogue/:id:/index',
        name: 'BookCatalogue',
        component: () =>
            import ('../views/BookCatalogue.vue'),
    },
    {
        path: '/ListenBook/:id',
        name: 'ListenBook',
        component: () =>
            import ('../views/ListenBook.vue'),
    },
    {
        path: '/RegisterAccount',
        name: 'RegisterAccount',
        component: () =>
            import ('../views/RegisterAccount.vue')
    },
    {
        path: '/SearchResult/:word/',
        name: 'SearchResult',
        component: () =>
            import ('../views/SearchResult.vue')
    },
    {
        path: '/AllRankList/:url/:i/',
        name: 'AllRankList',
        component: () =>
            import ('../views/AllRankList.vue')
    },
]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

export default router